-----------------------------------------------------------------------------------*/


/* ----------------------------------------------------------------
    Social Icons
-----------------------------------------------------------------*/


.social-icon {
    margin: 0 5px 5px 0;
    width: 40px;
    height: 40px;
    font-size: 20px;
    line-height: 38px !important;
    color: #555;
    text-shadow: none;
    border: 1px solid #AAA;
    .border-radius(3px);
    overflow: hidden;
}


/* Social Icons - Rounded
-----------------------------------------------------------------*/

.si-rounded { .border-radius(50%); }

/* Social Icons - Borderless
-----------------------------------------------------------------*/

.si-borderless { border-color: transparent !important; }

/* Social Icons - Dark
-----------------------------------------------------------------*/

.si-dark {
    background-color: #444;
    color: #FFF !important;
    border-color: transparent;
}

/* Social Icons - Light
-----------------------------------------------------------------*/

.si-light {
    background-color: rgba(0,0,0,0.05);
    color: #666 !important;
    border-color: transparent;
}

/* Social Icons - Colored
-----------------------------------------------------------------*/

.si-colored {
    border-color: transparent !important;
    i {
        color: #FFF;
    }
}

/* Social Icons - Large
-----------------------------------------------------------------*/

.social-icon {
    &.si-large {
        margin: 0 10px 10px 0;
        width: 56px;
        height: 56px;
        font-size: 30px;
        line-height: 54px !important;
    }
}

/* Social Icons - Small
-----------------------------------------------------------------*/

.social-icon {
    &.si-small {
        width: 32px;
        height: 32px;
        font-size: 14px;
        line-height: 30px !important;
    }
    i {
        display: block;
        position: relative;
    }
}

body:not(.device-touch) {
    .social-icon {
        i {
            -webkit-transition: all .3s ease;
            -o-transition: all .3s ease;
            transition: all .3s ease;
        }
    }
}

.social-icon {
    i {
        &:last-child {
            color: #FFF !important;
        }
    }
    &:hover {
        color: #555;
        border-color: transparent;
        i {
            &:first-child {
                margin-top: -38px;
            }
        }
    }
    &.si-large {
        &:hover {
            i {
                &:first-child {
                    margin-top: -54px;
                }
            }
        }
    }
    &.si-small {
        &:hover {
            i {
                &:first-child {
                    margin-top: -30px;
                }
            }
        }
    }
}
.si-colored {
    &.social-icon {
        border-color: transparent;
    }
}

/* Social Icons - Sticky
-----------------------------------------------------------------*/

.si-sticky {
    position: fixed;
    top: 50%;
    left: 5px;
    width: 36px;
    z-index: 100;
}

/* Social Icons - Sticky Right
-----------------------------------------------------------------*/

.si-sticky {
    &.si-sticky-right {
        left: auto;
        right: 8px;
    }
}

/* Social Icons - Share
-----------------------------------------------------------------*/

.si-share {
    position: relative;
    padding-left: 5px;
    border-top: 1px solid #EEE;
    border-bottom: 1px solid #EEE;
    span {
        line-height: 36px;
        font-weight: bold;
        font-size: 13px;
    }
    div {
        float: right;
    }
    .social-icon {
        margin: -1px 0;
        .border-radius(0);
        font-size: 15px;
    }
}

